module.exports = "import React from 'react';\nimport { Tabs,Button } from 'bricks-web';\nimport './index.less';\n\nexport default class Example extends React.Component{\n    constructor(props){\n        super(props);\n        this.state={\n            data:[\n                {label:\"首页\",key:'home'},\n                {label:\"邮箱\",key:'email'},\n                {label:\"动态\",key:'info'},\n            ],\n            selectedKey:'home'\n        }\n    }\n    renderItem = (key)=>{\n        return <div>{key}</div>\n    }\n    add = ()=>{\n        var key = new Date().valueOf().toString();\n        this.state.data.push({key:key,label:\"new\"});\n        this.setState({\n            data:this.state.data,\n            selectedKey:key\n        })\n    }\n\n    modify = () => {\n        this.state.data[0].label=\"修改了\";\n        this.setState({\n            data:this.state.data,\n        })\n    }\n    tabChange(record){\n            this.setState({\n                selectedKey:record.key\n            });\n    }\n    \n    render(){\n        return <div>\n             <div>\n                <Button onClick={this.add.bind(this)}>Add</Button>\n                <Button onClick={this.modify.bind(this)}>Modify</Button>\n            </div>\n            <Tabs \n                defaultSelectedKey={this.state.selectedKey}\n                onChange={this.tabChange.bind(this)}\n                style={{height:200}} \n                renderItem={this.renderItem.bind(this)} \n                data={this.state.data} />\n        </div>;\n    }\n}"