module.exports = "import React from 'react';\nimport { Tabs } from 'bricks-web';\nimport './index.less';\n\nexport default class Example extends React.Component{\n    renderItem = (key)=>{\n        return <div>{key}</div>\n    }\n    render(){\n        return <div>\n        <Tabs \n            defaultSelectedKey='home'\n            style={{height:200}}\n            tabPosition='left' \n            renderItem={this.renderItem.bind(this)} \n            data={[\n                {label:\"首页\",key:'home'},\n                {label:\"邮箱\",key:'email'},\n                {label:\"动态\",key:'info'},\n            ]} />\n        <br/>\n        <br/>\n        <Tabs \n            defaultSelectedKey='home'\n            style={{height:200}}\n            tabPosition='right' \n            renderItem={this.renderItem.bind(this)} \n            data={[\n                {label:\"首页\",key:'home'},\n                {label:\"邮箱\",key:'email'},\n                {label:\"动态\",key:'info'},\n            ]} />\n        </div>;\n    }\n}"